/*Brand Portal ***********************************************************************************************/
:root {
    --phone:480px;
    --tablet:768px;
    --desktop:980px;
    --widescreen:1200px;
    --color-white:#fff;
    --color-offwhite:#ede7d8;
    --color-black:#000;
    --color-navy:#354253;
    --color-dark:#23272a;
    --color-grey-1:#333;
    --color-grey-2:#4f4f4f;
    --color-grey-3:#828282;
    --color-grey-4:#bdbdbd;
    --color-grey-5:#e0e0e0;
    --color-grey-6:#f2f2f2;
    --color-grey-7:#f9f9f9;
    --color-blue:#39f;
    --color-blue-dark:#6953e7;
    --color-red:#eb5757;
    --color-green-1:#219653;
    --color-yellow:#f2c94c;
    --color-orange:#f2994a;
    --color-orange-100:#bd6f34;
    --color-orange-tint-80:#cd8249;
    --color-orange-tint-70:#d18d58;
    --color-orange-tint-50:#daa277;
    --color-orange-tint-40:#deac86;
    --color-orange-tint-30:#e2b695;
    --color-orange-tint-20:#e6c1a4;
    --color-orange-tint-10:#eacbb3;
    --color-orange-tint-6:#f3e0d2;
    --color-orange-tint-4:#f7eae1;
    --color-orange-tint-2:#fbf5f0;
    --color-orange-tint-1:#fffcf9;
    --color-orange-shade-100:#bd6f34;
    --color-orange-shade-90:#af6730;
    --color-orange-shade-80:#a15f2c;
    --color-orange-shade-70:#945728;
    --color-orange-shade-60:#864f25;
    --color-orange-shade-50:#794721;
    --color-orange-shade-40:#794721;
    --color-orange-shade-30:#5e371a;
    --color-orange-shade-20:#512f16;
    --color-orange-shade-10:#432812;
    --color-orange-shade-8:#36200f;
    --color-orange-shade-6:#28180b;
    --color-orange-shade-4:#1b1007;
    --color-orange-shade-2:#0d0804;
    --font-sans-serif:Mulish,sans-serif;
    --font-serif:"Times New Roman",Times,Baskerville,Georgia,serif;
    --base-background-color:var(--color-white);
    --base-font-family:var(--font-sans-serif);
    --base-font-size:var(--text);
    --base-line-height:1.42;
    --base-text-color:var(--color-navy);
    --base-border-radius:.4rem;
    --base-min-width:320px;
    --gap:15px;
    --base-link-color:var(--color-orange-100);
    --btn-color:var(--color-white);
    --btn-background:var(--color-orange-100);
    --btn-background-active:var(--color-white);
    --btn-height:2.5em;
    --btn-radius:.333em;
    --btn-padding:.5em 1em;
    --btn-font-size:1.2rem;
    --btn-font-family:var(--font-sans-serif);
    --form-font-family:var(--font-sans-serif);
    --form-font-size:1.4rem;
    --form-element-padding:.35714285714em .57142857142em;
    --form-element-border-color:var(--color-white);
    --form-element-focus-border-color:var(--color-orange-100);
    --placeholder-color: #808084;
    --headings-font-family:var(--font-sans-serif);
    --headings-color:var(--base-text-color);
    --headings-sizes-h1:2.4rem;
    --headings-sizes-h2:2rem;
    --headings-sizes-h3:1.8rem;
    --headings-sizes-h4:1.6rem;
    --headings-sizes-h5:1.4rem;
    --headings-sizes-h6:1.2rem;
    --text:1.4rem;
    --h-height-top: 5.2rem;
    --h-height-bottom: 4rem;
    --h-height:calc(var(--h-height-bottom) + var(--h-height-top));
    --color-blue:#2F80ED;
    --h-pos:fixed;--h-z:10;--h-pd-top:0.6rem .8rem 0.6rem 2.4rem;--h-pd-bottom:.5rem 1.6rem;--h-fz:inherit;--h-bg:var(--color-white);
    
    --h-height-top-product: 6.2rem;
    --h-height-bottom-product: 0rem;
    --h-height-product: calc(var(--h-height-bottom-product) + var(--h-height-top-product));
    
    --h-mp-height-bottom: 1rem;
    --h-mp-height: calc(var(--h-mp-height-bottom) + var(--h-height-top));
    }
    .clearfix:after{content:"";display:block;clear:both}
    .sr-only{position:absolute;width:1px;height:1px;margin:-1px;padding:0;overflow:hidden;clip:rect(0,0,0,0);border:0}
    .sr-only-focusable:active,.sr-only-focusable:focus{position:static;width:auto;height:auto;margin:0;overflow:visible;clip:auto}
    .ellipsis{overflow:hidden;white-space:nowrap;text-overflow:ellipsis}
    *,:before,:after{box-sizing:border-box}
    ::-webkit-scrollbar{width:1rem;height:.6rem}
    ::-webkit-scrollbar-track{background-color:var(--color-orange-tint-1);border-radius:.4rem}
    ::-webkit-scrollbar-thumb{border-radius:.4rem;background:var(--color-grey-5)}
    html{font-size:62.5%}
    body{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:var(--base-text-color);background:var(--base-background-color);font:var(--base-font-size)/var(--base-line-height) var(--base-font-family);min-width:var(--base-min-width);font-weight:400;overflow-anchor:none}
    img{max-width:100%;height:auto}
    .gm-style img{max-width:none}
    h1,.h1,h2,.h2,h3,.h3,h4,.h4,h5,.h5,h6,.h6,.h{font-weight:700;margin:0;line-height:1.5}
    h1:not(:last-child),.h1:not(:last-child),h2:not(:last-child),.h2:not(:last-child),h3:not(:last-child),.h3:not(:last-child),h4:not(:last-child),.h4:not(:last-child),h5:not(:last-child),.h5:not(:last-child),h6:not(:last-child),.h6:not(:last-child),.h:not(:last-child){margin-bottom:3.2rem}
    h1,.h1{font-size:var(--headings-sizes-h1)}
    h2,.h2{font-size:var(--headings-sizes-h2)}
    h3,.h3{font-size:var(--headings-sizes-h3)}
    h4,.h4{font-size:var(--headings-sizes-h4)}
    h5,.h5{font-size:var(--headings-sizes-h5);font-weight:800}
    h6,.h6{font-size:var(--headings-sizes-h6)}
    p{margin:0}
    a{color:inherit}
    a:focus{text-decoration:none}
    strong{font-weight:600}
    .yellow{color:var(--color-yellow)!important}
    form,fieldset{margin:0;padding:0;border-style:none}
    input[type=text],input[type=tel],input[type=email],input[type=search],input[type=password],textarea{-webkit-appearance:none;appearance:none;box-sizing:border-box;font-family:var(--form-font-family);border-radius:0;border:.1rem solid var(--form-element-border-color);padding:var(--form-element-padding);outline:none}
    input[type=text]:focus,input[type=tel]:focus,input[type=email]:focus,input[type=search]:focus,input[type=password]:focus,textarea:focus{border-color:var(--form-element-focus-border-color)}
    input[type=text]::-webkit-input-placeholder,input[type=tel]::-webkit-input-placeholder,input[type=email]::-webkit-input-placeholder,input[type=search]::-webkit-input-placeholder,input[type=password]::-webkit-input-placeholder,textarea::-webkit-input-placeholder{color:var(--placeholder-color)}
    input[type=text]::-moz-placeholder,input[type=tel]::-moz-placeholder,input[type=email]::-moz-placeholder,input[type=search]::-moz-placeholder,input[type=password]::-moz-placeholder,textarea::-moz-placeholder{opacity:1;color:var(--placeholder-color)}
    input[type=text]:-moz-placeholder,input[type=tel]:-moz-placeholder,input[type=email]:-moz-placeholder,input[type=search]:-moz-placeholder,input[type=password]:-moz-placeholder,textarea:-moz-placeholder{color:var(--placeholder-color)}
    input[type=text]:-ms-input-placeholder,input[type=tel]:-ms-input-placeholder,input[type=email]:-ms-input-placeholder,input[type=search]:-ms-input-placeholder,input[type=password]:-ms-input-placeholder,textarea:-ms-input-placeholder{color:var(--placeholder-color)}
    input[type=text].placeholder,input[type=tel].placeholder,input[type=email].placeholder,input[type=search].placeholder,input[type=password].placeholder,textarea.placeholder{color:var(--placeholder-color)}
    select{border-radius:0}
    textarea{resize:vertical;vertical-align:top}
    button,input[type=button],input[type=reset],input[type=file],input[type=submit]{-webkit-appearance:none;appearance:none;border-radius:0;cursor:pointer;font-family:var(--form-font-family)}
    .icon{fill:currentColor;stroke:currentColor;color:inherit;width:100%;height:100%}
    .logo{position:relative;display:flex;align-items:center;align-content:center;justify-content:inherit;flex-wrap:nowrap;width:100%;max-width:50px}
    .logo picture{position:relative;width:100%;height:100%;display:flex;align-items:center;align-content:center;justify-content:inherit;flex-wrap:nowrap}
    .logo img{width:100%;height:auto}
    .image{position:relative;width:100%;height:auto}
    .image picture{position:relative;display:flex;align-items:center;align-content:center;justify-content:inherit;flex-wrap:nowrap;width:100%;height:100%;z-index:1}
    .image img{width:100%}
    .image--cover{min-height:100%;height:auto}
    .image--cover img{position:absolute;left:0;top:0;width:100%;height:100%;object-fit:contain}
    .table-vars .image--1-1{border: 1px solid var(--color-grey-5)}
    .image--1-1 picture{padding-top:100%}
    .image--3-2 picture{padding-top:66.66%}
    .image--3-4 picture{padding-top:75%}
    .image--4-5 picture{padding-top:120%}
    .image--16-9 picture{padding-top:56.25%}
    .swiper-button-prev,.swiper-button-next{position:absolute;margin:0;top:50%;transform:translateY(-50%);display:flex;align-items:center;align-content:center;justify-content:center;flex-wrap:nowrap;width:1.6rem;height:1.6rem;background:transparent;transition:all .3s ease-in-out;color:var(--color-white)}
    .swiper-button-prev:hover,.swiper-button-next:hover{color:var(--color-navy)}
    .swiper-button-prev .icon,.swiper-button-next .icon{width:1rem;height:1.6rem}
    .swiper-button-prev:after,.swiper-button-next:after{display:none}
    .swiper-button-prev{justify-content:flex-start;left:.6rem}
    .swiper-button-next{justify-content:flex-end;right:.6rem}
    .swiper-pagination{bottom:.6rem!important;left:0;width:100%}
    .swiper-pagination-bullet{width:1.6rem;height:.4rem;border-radius:.4rem;background-color:var(--color-white);margin:0 .4rem;opacity:1}
    .swiper-pagination-bullet-active{background-color:var(--color-yellow)}
    .checkbox{position:relative;width:100%;width:100%}
    .checkbox input{position:absolute;left:100%;opacity:0;width:0;height:0}
    .checkbox label{position:relative;display:flex;align-items:center;align-content:center;width:100%}
    .checkbox label:hover{cursor:pointer}
    .checkbox--no-decor .checkbox-text{position:relative;width:100%;border-radius:10rem;padding:.8rem 1.2rem;background:var(--color-grey-7);color:var(--color-grey-3);transition:all .3s ease-in-out;border:.1rem solid var(--color-grey-7)}
    .checkbox--no-decor .checkbox-text strong{font-weight:700;color:var(--color-navy)}
    .checkbox--no-decor .checkbox-text:after{content:"";position:absolute;width:1.2rem;height:1.2rem;-webkit-mask-size:contain;-webkit-mask-position:center center;-webkit-mask-repeat:no-repeat;background-color:currentColor;-webkit-mask-image:url(../images/icons/icon-close.svg);mask-image:url(../images/icons/icon-close.svg);opacity:0;visibility:hidden;right:.8rem;top:0;bottom:0;margin:auto 0;transition:all .3s ease-in-out}
    .checkbox--no-decor label:hover .checkbox-text{border-color:var(--color-navy);color:var(--color-navy)}
    .checkbox--no-decor label:hover .checkbox-text:after{background-color:#56ccf2}
    .checkbox--no-decor input:checked ~ .checkbox-text,.checkbox--no-decor input[checked] ~ .checkbox-text{border-color:var(--color-navy);background:var(--color-navy);color:var(--color-white)}
    .checkbox--no-decor input:checked ~ .checkbox-text strong,.checkbox--no-decor input[checked] ~ .checkbox-text strong{color:inherit}
    .checkbox--no-decor input:checked ~ .checkbox-text:after,.checkbox--no-decor input[checked] ~ .checkbox-text:after{opacity:1;visibility:visible}
    .checkbox--tabs{position:relative;display:flex}
    .checkbox--tabs .checkbox-text:after{display:none}
    .checkbox--tabs label:nth-child(1) .checkbox-text{border-top-right-radius:0;border-bottom-right-radius:0}
    .checkbox--tabs label:nth-child(2) .checkbox-text{border-top-left-radius:0;border-bottom-left-radius:0}
    .checkbox--toggler label{height:100%}
    .checkbox--toggler .checkbox-toggler{position:relative;width:3.6rem;min-width:3.6rem;height:2rem;border-radius:10rem;background:var(--color-grey-5);transition:all .3s ease-in-out}
    .checkbox--toggler .checkbox-toggler span{position:absolute;display:inline-block;left:.6rem;top:0;bottom:0;margin:auto 0;width:.8rem;height:1.2rem;border-radius:10rem;background:var(--color-white);box-shadow:0 2px 6px rgba(0,0,0,0.1);transition:all .3s cubic-bezier(0.175,0.885,0.32,1.275)}
    .checkbox--toggler input:checked ~ .checkbox-toggler{background:var(--color-green-1)}
    .checkbox--toggler input:checked ~ .checkbox-toggler span{left:2.2rem}
    .select{position:relative;width:100%}
    .select:not(:last-child){margin-bottom:.8rem}
    .select select{position:relative;width:100%;height:3.2rem;padding:.2rem 1.2rem;border:.1rem solid var(--color-grey-4);color:var(--color-grey-3);-webkit-appearance:none;appearance:none;background:url(../images/icons/icon-polygon--down.svg),#fff;background-repeat:no-repeat;background-position:center right 1.2rem;background-size:.8rem auto}
    .wrapper{display:flex;flex-direction:column;min-height:100vh}
    .content{flex:1 0 auto;flex-direction:column;padding-top:var(--h-height)}
    .section{--section-pd:0 0;--section-bg:transparent;position:relative;width:100%;padding:var(--section-pd);background:var(--section-bg);overflow:hidden}
    .container{position:relative;z-index:1;padding:0 var(--gap)}
    .row{margin:0 calc(-1 * var(--gap))}
    .row > div{padding:0 var(--gap)}
    .header{position:var(--h-pos);width:100%;background:var(--h-bg);font-size:var(--h-fz);box-shadow:0 1px 0 rgba(0,0,0,0.12);z-index:var(--h-z);height:var(--h-height)}
    .header_block{position:relative}
    .header_block-top{padding:var(--h-pd-top);height:var(--h-height-top)}
    .header_block-bottom{padding:var(--h-pd-bottom);height:var(--h-height-bottom);background:var(--color-orange-tint-2);overflow:hidden}
    .header_container{--h-c-gap:1.6rem;display:flex;align-items:center;align-content:center;justify-content:inherit;flex-wrap:nowrap;gap:var(--h-c-gap)}
    .header_search{flex:1}
    .header_menu{position:relative}
    .header_submenu{position:relative;overflow:auto;padding-bottom:17px}
    .header_submenu-scroll-button{position:absolute;top:0;bottom:0;margin:auto 0;z-index:2;width:2.4rem;height:3rem;background:var(--color-white);border:none;display:flex;align-items:center;align-content:center;justify-content:center;flex-wrap:nowrap;color:var(--color-grey-3);opacity:0;pointer-events:none}
    .header_submenu-scroll-button.visible{opacity:1;pointer-events:all}
    .header_submenu-scroll-button:hover{color:var(--color-orange-100)}
    .header_submenu-scroll-button .icon{width:.8rem;height:.8rem}
    .header_submenu-scroll-button-next{border-radius:0 .4rem .4rem 0;right:.8rem}
    .header_submenu-scroll-button-prev{border-radius:.4rem 0 0 .4rem;left:.8rem}
    .header_submenu-content{position:absolute;left:0;top:100%;width:100%;height:calc(var(--vh,1vh) * 100 - var(--h-height));overflow:auto;opacity:0;visibility:hidden}
    .header_submenu-content.open{opacity:1;visibility:visible}
    .header_submenu-content-item{position:absolute;left:0;top:0;height:100%;width:100%;display:flex;flex-wrap:wrap;gap:3.2rem;padding:2.4rem;opacity:0;visibility:hidden;background:var(--color-white);transition:all .3s ease-in-out;z-index:0}
    .header_submenu-content-item.active{opacity:1;visibility:visible;z-index:1}
    .header_submenu-category{position:relative;font-size:1.2rem;list-style:none;margin:0;padding:0}
    .header_submenu-category li{position:relative;display:block}
    .header_submenu-category li:not(:last-child){margin-bottom:1rem}
    .header_submenu-category a{position:relative;text-decoration:none;color:var(--color-grey-3);transition:color .3s ease-in-out}
    .header_submenu-category a.active,.header_submenu-category a:hover{color:var(--color-navy);font-weight:700}
    .header_submenu-category-wrap{position:relative;width:calc((100% - 5*3.2rem) / 6)}
    .header_submenu-category-title{position:relative;font-weight:700;font-size:1.4rem;min-height:4rem;margin-bottom:.8rem;box-shadow:0 1px 0 rgba(0,0,0,0.24)}
    .header_actions{--h-a-gap:1.6rem;position:relative;display:flex;align-items:center;align-content:center;justify-content:inherit;flex-wrap:nowrap;gap:var(--h-a-gap);padding-left:.8rem}
    .header_action{--h-a-color:var(--color-grey-3);--h-a-color-active:var(--color-navy);position:relative;display:flex;align-items:center;align-content:center;justify-content:flex-start;flex-wrap:nowrap;transition:color .3s ease-in-out;border:none;padding:0;background:transparent}
    .header_action-mails,.header_action-notifications{width:3.2rem;height:3.2rem;color:var(--h-a-color)}
    .header_action-mails:hover,.header_action-notifications:hover{cursor:pointer;color:var(--h-a-color-active)}
    .header_action .icon{width:2.4rem;height:2.4rem}
    .header_action .text{position:absolute;min-width:1.8rem;width:1.8rem;border-radius:1.8rem;height:1.8rem;background:var(--color-red);color:var(--color-white);display:flex;align-items:center;align-content:center;justify-content:center;flex-wrap:nowrap;font-weight:700;font-size:1.2rem;right:0;top:-.6rem;overflow:hidden;opacity:0}
    .header_action .text.visible{opacity:1}
    .header_action-notifications .dropdown_body{left:auto;right:0;width:24rem;padding:.8rem;background:var(--color-grey-7);max-height:32rem;overflow:auto}
    .header_action-notifications .dropdown_inner{padding:0}
    .header_action-notifications .dropdown_inner ul{text-align:left;font-size:1.2rem;letter-spacing:.02em;font-weight:400}
    .header_action-notifications .dropdown_inner ul li{color:#2B4053;padding:.8rem 1.2rem;background:var(--color-white)}
    .header_action-notifications .dropdown_inner ul li:not(:last-child){margin-bottom:.8rem}
    .header_action-notifications .dropdown_inner ul li span{color:#D46F2C}
    .footer{--f-bg:var(--color-ghostwhite);--f-fz:inherit;--f-pd-top:17.6rem 0 3rem 0;--f-pd-bottom:3.2rem 0;position:relative}
    .footer_menu{position:relative;display:flex;align-items:center;align-content:center;justify-content:flex-start;flex-wrap:wrap;list-style:none;padding:0;margin:0;gap:1rem}
    .footer_menu li{position:relative;display:block;width:100%}
    .footer_menu a{position:relative;display:inline-block;text-decoration:none;transition:color .3s ease-in-out}
    .footer_menu a:focus,.footer_menu a:hover,.footer_menu a.active{color:var(--color-indigo)}
    .footer_menu-wrap{position:relative}
    .button{position:relative;display:inline-flex;align-items:center;align-content:center;justify-content:center;background:var(--btn-background);padding:var(--btn-padding);height:var(--btn-height);border-radius:var(--btn-radius);border:.2rem solid var(--btn-background);font-size:var(--btn-font-size);font-family:var(--btn-font-family);font-weight:700;letter-spacing:.05em;color:var(--btn-color);text-decoration:none;transition:all .3s ease-in-out;gap:1rem}
    .button:hover{background:transparent;border-color:var(--btn-background);color:var(--btn-background)}
    .button:active{background:var(--btn-background-active);border-color:var(--btn-background-active)}
    .button-dark{--btn-background:var(--color-navy);--btn-color:var(--color-white)}
    .button-red{--btn-background:var(--color-red);--btn-color:var(--color-white)}
    .button-orange{--btn-background:var(--color-orange);--btn-color:var(--color-white)}
    .button-green{--btn-background:var(--color-green-1);--btn-color:var(--color-white)}
    .button-white{--btn-background:var(--color-white);--btn-color:var(--color-grey-3);border-color:var(--color-grey-3)}
    .button-white:hover{background:var(--color-grey-3);border-color:var(--color-grey-3)}
    .button-grey{--btn-background:var(--color-grey-6);--btn-color:var(--color-navy)}
    .button-grey:hover{background:var(--color-navy);color:var(--color-white);border-color:var(--color-navy)}
    .button-grey-3{--btn-background:var(--color-grey-3);--btn-color:var(--color-white)}
    .button-grey-3:hover{background:var(--color-grey-3);color:var(--color-white);border-color:var(--color-grey-3)}
    .button .icon{width:1.6rem;height:1.6rem}
    .button.large{--btn-font-size:1.4rem;--btn-height:4rem}
    .button[disabled]{--btn-background:var(--color-orange-tint-20);--btn-color:var(--color-white);pointer-events:none}
    .button.bordered{color:var(--btn-background);background-color:transparent}
    .button.bordered:hover{color:var(--btn-color);background-color:var(--btn-background)}
    .button-blue{--btn-background:var(--color-blue);--btn-color:var(--color-white);border-color:var(--color-blue)}
    .button-blue:hover{background:var(--color-white);--btn-color:var(--color-blue);border-color:var(--color-blue)}
    @keyframes loading {
    0%{transform:rotate(0)}
    100%{transform:rotate(270deg)}
    }
    .menu{--m-fz:1.4rem;--m-color:var(--color-grey-3);--m-color-active:var(--color-navy);--m-bg-active:var(--color-orange-tint-2);--m-gap:.8rem 1.6rem;position:relative;list-style:none;padding:0;margin:0;font-size:var(--m-fz);letter-spacing:.05em;display:flex;align-items:center;align-content:center;justify-content:inherit;flex-wrap:nowrap}
    .menu-open .header_nav{right:0;opacity:1;visibility:visible}
    .menu_item{position:relative;display:flex;align-items:center;align-content:center;justify-content:center;flex-wrap:wrap}
    .menu_item.with-child{position:relative}
